*{margin: 0;padding: 0;box-sizing: border-box;}
ul,li{list-style: none;}
a{color: #000;text-decoration: none;}
input{outline: none;}
.inner{width: 1280px;margin: 0 auto;}
header{width: 100%;height: 80px;line-height: 80px;}
header .inner{width: 1280px;margin: 0 auto; display: flex; justify-content: space-between;}
.search_area{display: flex;gap: 10px; align-items: center;}
.search_area input{width:400px; height: 20px;}
.my ul{display: flex; gap: 10px;}

#search h2{text-align: center;}
#search ul {display: flex;flex-wrap: wrap; justify-content: space-between;}
#search .card {width:24%;height: 400px; border-radius: 10px;overflow: hidden;box-shadow: 0 5px 10px rgba(0, 0, 0, .2); margin-bottom: 14px; position: relative;}
#search .card img{display: block; width: 100%;height: 100%;}
#search .details{position: absolute;width: 100%;height:100%; background-color: rgba(0, 0, 0, 0.8); z-index: 2;left: 0;bottom: -100%; transition: 1s; padding: 1.5em 1.5em 2em;}
#search .details-active{bottom: 0;}
#search .details h3{color: #fff;font-weight: bold;margin-bottom: 10px;}
.details p {color: #fff;margin-bottom: 8px;}
p.author{opacity: 0.6;}

.details p.description{height:68px; overflow: hidden;text-overflow: ellipsis; display: -webkit-box; -webkit-line-clamp: 3;-webkit-box-orient: vertical; opacity: 0.8;}
p.price{text-align: right;color: yellow;opacity: 0.7;}

/* 페이징 */
.paging {width: 60%; margin: 0 auto;}
.paging ul {display: flex;}
.paging ul li {display: inline-block;width: 36px;height: 36px;border: 1px solid #ddd;text-align: center;line-height: 36px; cursor: pointer;}
.paging ul li.on {background-color: #d0d0d0;}